@import "main-bx";

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  &_fixed {
    position: fixed;
    animation: headerAnimation $transition forwards;
  }
  &_white {
    .logo {
      img {
        opacity: 0;
        &.logo-white {
          opacity: 1;
        }
      }
    }
    .nav {
      &__link {
        color: $color_gray;
        &:hover {
          background-color: $color_gray;
          color: $color_red;
        }
      }
    }
    .burger {
      span {
        background-color: $color_gray;
      }
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@keyframes headerAnimation {
  0% {
    transform: translateY(-150%);
  }
  100% {
    transform: translateY(0);
  }
}

@media (max-width: 48em) {
  .header {
    &.header_white {
      .nav {
        background-color: $color_red;
      }
    }
    &__content {
      padding: 3rem 0;
    }
    .nav {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      padding: 15rem 3rem;
      background-color: $color_bg;
      transition: all 0.4s ease;
      transform: translateY(-150%);
      z-index: -1;
      overflow-x: hidden;
      overflow-y: auto;
      &_active {
        transform: translateY(0%);
      }
    }
  }
}