@font-face {
  font-family: 'TT Commons';
  src: local('TT Commons Regular'), local('TT-Commons-Regular'),
    url('../fonts/TTCommons/TTCommons-Regular.woff2') format('woff2'),
    url('../fonts/TTCommons/TTCommons-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

