@import "main-bx";

.feedback {
  padding: 18.3rem 0 13.7rem;
  &__title {
    margin-bottom: 2rem;
  }
  .form {
    max-width: 61.3rem;
    margin: 0 auto;
  }
}

@media (max-width: 48em) {
  .feedback {
    padding: 35.2rem 0 28.2rem;
    &__title {
      margin-bottom: 8rem;
    }
    .form {
      max-width: none;
    }
  }
}