@import "main-bx";

.burger {
  display: none;
}

@media (max-width: 48em) {
  .burger {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 6rem;
    height: 6rem;
    flex-shrink: 0;
    position: relative;
    z-index: 1;
    transition: all $transition;
    span {
      width: 100%;
      height: .6rem;
      background-color: $color_black;
      transition: all $transition;
      &:first-child {
        margin-bottom: .6rem;
      }
      &:last-child {
        width: 33.3%;
        margin-left: auto;
      }
    }
    &_active {
      span {
        &:first-child {
          transform: rotate(45deg);
          margin-bottom: -.6rem;
        }
        &:last-child {
          width: 100%;
          margin: 0;
          transform: rotate(-45deg);
        }
      }
    }
  }
}