@import "main-bx";

.link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 9.9rem;
  height: 9.9rem;
  border-radius: 50%;
  border: 1px solid $color_black;
  background-color: $color_gray;
  // transition: all $transition;
  &:hover {
    border-color: $color_red;
    > svg {
      stroke: $color_red;
      &.fill {
        fill: $color_red;
      }
    }
    .link__text {
      svg {
        fill: $color_red;
      }
    }
  }
  &:active {
    border-color: $color_red_hover;
    > svg {
      stroke: $color_red_hover;
      &.fill {
        fill: $color_red_hover;
      }
    }
    .link__text {
      svg {
        fill: $color_red_hover;
      }
    }
  }
  svg {
    transition: all $transition;
  }
  > svg {
    width: 3.7rem;
    height: 3.7rem;
    stroke: $color_black;
    &.stroke {
      stroke-width: 5px;
    }
  }
  &__text {
    position: absolute;
    inset: -2rem;
    svg {
      width: 100%;
      height: 100%;
    }
  }
}

@media (max-width: 48em) {
  .link {
    width: 14.8rem;
    height: 14.8rem;
    > svg {
      width: 5.4rem;
      height: 5.4rem;
    }
    &__text {
      inset: -3rem;
    }
  }
}