@import "main-bx";

.form {
  &__content {
    margin-bottom: 4rem;
  }
  &__bottom {
    display: flex;
    align-items: center;
  }
  &__button {
    margin-right: 1.5rem;
  }
  &__privacy {
    font-size: 1.4rem;
  }
}

input {
  width: 100%;
  height: 6rem;
  border: 1px solid $color_gold;
  border-radius: 0;
  background-color: $color_gray;
  padding: 0 1.9rem;
  appearance: none;
  font-size: 2rem;
  color: $color_black;
  transition: all $transition;
  &:hover, &:focus {
    border-color: $color_black;
  }
  &:not(:placeholder-shown):valid {
    border-color: $color_black;
  }
  &:checked ~ .label__box {
    border-color: $color_red;
    &::after {
      opacity: 1;
    }
  }
  &::placeholder {
    color: $color_gold;
  }
  &.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}

.label {
  width: 100%;
  display: inline-flex;
  cursor: pointer;
  &:not(:last-child) {
    margin-bottom: 1rem;
  }
  &_checkbox {
    display: flex;
    -webkit-tap-highlight-color: transparent;
    &:hover {
      .label__box {
        border-color: $color_red;
      }
    }
  }
  &__box {
    width: 1rem;
    height: 1rem;
    flex-shrink: 0;
    border: 1px solid $color-black;
    margin-right: .5rem;
    position: relative;
    transition: all $transition;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: .4rem;
      height: .4rem;
      opacity: 0;
      transition: opacity $transition;
      background-color: $color_red;
    }
  }
}

@media (max-width: 48em) {
  .form {
    &__content {
      margin-bottom: 6rem;
    }
    &__bottom {
      display: flex;
      flex-direction: column;
    }
    &__button {
      width: 100%;
      margin-right: 0;
      margin-bottom: 2.4rem;
    }
    &__privacy {
      font-size: 2.8rem;
    }
  }

  input {
    height: 12rem;
    padding: 0 3.4rem;
    font-size: 4rem;
  }

  .label {
    width: 100%;
    &:not(:last-child) {
      margin-bottom: 2rem;
    }
    &__box {
      width: 2rem;
      height: 2rem;
      margin-right: 1rem;
      &::after {
        width: .8rem;
        height: .8rem;
      }
    }
  }
}