@import "main-bx";

.nav {
  margin-right: -1.5rem;
  &__list {
    display: flex;
    align-items: center;
    li {
      font-size: 2rem;
      line-height: 1.5;
      &:not(:last-child) {
        margin-right: 1rem;
      }
    }
  }
  &__link {
    display: inline-block;
    padding: 2.8rem 1.5rem;
    transition: all $transition;
    &:hover {
      background-color: $color_red;
      color: $color_white;
    }
  }
}

@media (max-width: 48em) {
  .nav {
    margin-right: 0;
    &__list {
      flex-direction: column;
      align-items: unset;
      li {
        font-size: 4rem;
        &:not(:last-child) {
          margin-right: 0;
        }
      }
    }
    &__link {
      display: block;
      padding: 5.6rem 3rem;
    }
  }
}