@import "main-bx";

.title {
  font-size: 10rem;
  @include soviet;
  line-height: 1.5;
  letter-spacing: 0.02em;
  &_2 {
    font-size: 6rem;
    line-height: 2.5;
    text-align: center;
  }
}

@media (max-width: 48em) {
  .title {
    font-size: 8rem;
    text-align: center;
    &_2 {
      font-size: 8rem;
      line-height: 1.5;
    }
  }
}