@import "main-bx";

.footer {
  padding: 3rem 0;
  background-color: $color_black;
  color: $color_gray;
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 2rem;
  }
  &__copy {
    span {
      margin-right: .8rem;
    }
  }
  &__creator {
    color: #5A636B;
    span {
      color: $color_gray;
      text-decoration: underline;
      transition: all $transition;
    }
    &:hover {
      span {
        color: $color_red;
      }
    }
  }
}

@media (max-width: 48em) {
  .footer {
    &__content {
      flex-direction: column;
      justify-content: center;
      font-size: 2.8rem;
    }
    &__copy {
      margin-bottom: 1.8rem;
    }
  }
}