@import "main-bx";

.hero {
  position: relative;
  z-index: 1;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 45.3rem;
    height: 100%;
    background-color: $color_red;
    z-index: -2;
    clip-path: polygon(20% 0, 100% 0, 100% 100%, 0% 100%);
  }
  &__img {
    position: absolute;
    bottom: 0;
    right: 1.4rem;
    width: 71.7rem;
    height: 73.2rem;
    z-index: -1;
    img {
      @include contain;
    }
  }
  &__content {
    min-height: 80rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10rem 0;
  }
  &__text {
    max-width: 65%;
  }
  &__suptitle {
    font-size: 3rem;
    line-height: 1.17;
    font-style: italic;
    color: $color_red;
    margin-bottom: -.3rem;
  }
  &__title {
    margin-left: -1rem;
    margin-bottom: 5rem;
  }
}

@media (max-width: 48em) {
  .hero {
    &::after {
      top: auto;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 38rem;
      z-index: -2;
      clip-path: polygon(0 0, 100% 20%, 100% 100%, 0% 100%);
    }
    &__img {
      bottom: 5rem;
      right: auto;
      left: -5rem;
      width: 78rem;
      height: auto;
    }
    &__content {
      min-height: auto;
      justify-content: flex-start;
      padding: 20rem 0 97rem;
    }
    &__text {
      max-width: none;
      text-align: center;
    }
    &__suptitle {
      font-size: 4rem;
      line-height: 1.15;
      margin-bottom: 2rem;
    }
    &__title {
      margin-left: 0;
      margin-bottom: 6rem;
    }
  }
}