@import "main-bx";

*, *::before, *::after {
  box-sizing: border-box;
}

html {
  font-family: $font_base;
  // font-size: 0.520835vw;
  font-size: 0.732065vw;
  font-style: normal;
  font-weight: normal;
  -webkit-animation: bugfix infinite 1s;
  line-height: 1.2;
  margin: 0;
  padding: 0;
  @media (min-width: 1921px) {
    font-size: 10px;
  }
  @media (min-width: 1400px) {
    font-size: .621vw;
  }
}

body {
  font-style: normal;
  font-weight: normal;
  -webkit-animation: bugfix infinite 1s;
  line-height: 1.2;
  margin: 0;
  padding: 0;
  color: $color_black;
  background-color: $color_bg;
}

input, textarea {
  -webkit-animation: bugfix infinite 1s;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

a {
  color: unset;
}

a, a:hover {
  text-decoration: none
}

button, input, a, textarea {
  outline: none;
  cursor: pointer;
  font: inherit;
  &:focus, &:active {
    outline: none
  }
}

h1, h2, h3, h4, h5, h6 {
  font: inherit;
  margin: 0;
  padding: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
  // image-rendering: -webkit-optimize-contrast;
}

ul, ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
  li {
    margin: 0;
    padding: 0;
  }
}

.container {
  max-width: 124.6rem;
  margin: 0 auto;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

:focus {
  outline-style: none;
  outline-width: 0 !important;
  outline-color: none !important;
}

.mobile {
  display: none !important;
}

.o-hidden {
  overflow: hidden;
}

.d-none {
  display: none !important;
}

@media (max-width: 48em) {
  html {
    font-size: 5px;
    font-size: 1.5625vw;
    font-size: 100 / 375 * 5vw;
    -webkit-text-size-adjust: none;
  }

  body {
    -webkit-text-size-adjust: none;
    // position: relative;
  }

  .container {
    padding: 0 2rem;
    width: 100%;
  }

  .desktop {
    display: none !important;
  }

  .mobile {
    display: block !important;
  }
}