@import "main-bx";

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 5.7rem;
  padding: 0 4.5rem;
  border: none;
  background-color: $color_red;
  font-size: 2rem;
  color: $color_gray;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  transition: all $transition;
  span {
    transform: translateY(.2rem);
  }
  &:hover {
    box-shadow: .5rem .5rem 0 $color_red_hover;
  }
  &:active {
    background-color: $color_red_hover;
    box-shadow: none;
  }
  &_skewd {
    height: 5rem;
    padding: 0 5.2rem;
    margin: 0 .5rem;
    border-radius: .2rem;
    transform: skewX(-15deg);
    span {
      transform: translateY(.2rem) skewX(15deg);
    }
  }
}

@media (max-width: 48em) {
  .button {
    height: 12rem;
    padding: 0 10rem;
    font-size: 4rem;
    span {
      transform: translateY(.4rem);
    }
    &:hover {
      box-shadow: 1rem 1rem 0 $color_red_hover;
    }
    &_skewd {
      height: 8.8rem;
      padding: 0 10rem;
      margin: 0 1rem;
      border-radius: .4rem;
      span {
        transform: translateY(.4rem) skewX(15deg);
      }
    }
  }
}