@import "main-bx";

.gallery {
  background-color: $color_red;
  overflow: hidden;
  &__slider {
    overflow: visible;
    height: 80rem;
    &-item {
      padding: 10.8rem 0;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      overflow: hidden;
      &.swiper-slide-active {
        .gallery__slider {
          &-media {
            transform: none;
            opacity: 1;
          }
          &-link {
            transform: translate(50%, 50%);
            opacity: 1;
            transition: all 1s ease-in-out;
          }
        }
      }
      &.swiper-slide-prev {
        .gallery__slider {
          &-media {
            transform: translate(-15rem, -60rem) rotate(-15deg);
          }
        }
      }
    }
    &-text {
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      transform: translateY(-50%);
      .gallery__ticker {
        &:last-child {
          .gallery__ticker-elem {
            &:nth-child(even) {
              -webkit-text-fill-color: currentcolor;
              -webkit-text-stroke: 0 currentcolor;
            }
            &:nth-child(odd) {
              -webkit-text-fill-color: transparent;
              -webkit-text-stroke: .2rem $color_gray;
            }
          }
        }
      }
    }
    &-card {
      width: 43.8rem;
      // height: 58.4rem;
      height: 100%;
      position: relative;
    }
    &-media {
      width: 100%;
      height: 100%;
      background-color: $color_gray;
      transform: translate(-15rem, 60rem) rotate(15deg);
      opacity: 0;
      transition: all 1s;
      video {
        width: 100%;
        height: 100%;
        object-fit: cover
      }
      img {
        @include cover;
      }
    }
    &-link {
      position: absolute;
      bottom: 0;
      right: 0;
      transform: translate(50%, 100%);
      z-index: 1;
      opacity: 0;
      transition: none;
    }
  }
  &__ticker {
    &-wrapper {
      transition-timing-function: linear;
    }
    &-item {
      width: auto;
      display: flex;
      align-items: center;
      flex-shrink: 0;
    }
    &-elem {
      font-size: 12rem;
      font-weight: 700;
      color: $color_gray;
      font-style: italic;
      text-transform: uppercase;
      letter-spacing: .03em;
      margin-right: 4rem;
      &:nth-child(even) {
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke: .2rem $color_gray;
      }
    }
  }
}

.js-animate {
  &.js-animate-end {
    .gallery {
      &__ticker {
        &:first-child {
          left: unset;
        }
        &:last-child {
          right: unset;
        }
      }
    }
  }
  .gallery {
    &__ticker {
      overflow: visible;
      &:first-child {
        left: 100%;
      }
      &:last-child {
        right: 100%;
      }
    }
  }
}

@media (max-width: 48em) {
  .gallery {
    &__slider {
      height: 162.4rem;
      &-item {
        padding: 35rem 0 39.2rem;
      }
      &-text {
        top: 0;
        bottom: 0;
        transform: none;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 10.6rem 0 0;
        .gallery__ticker {
          &:last-child {
            .gallery__ticker-elem {
              &:nth-child(odd) {
                -webkit-text-stroke: .4rem $color_gray;
              }
            }
          }
        }
      }
      &-card {
        width: 66rem;
      }
      &-link {
        right: 50%;
        transform: translate(50%, 100%);
      }
    }
    &__ticker {
      margin: 0;
      &-elem {
        font-size: 24rem;
        letter-spacing: .01em;
        &:nth-child(even) {
          -webkit-text-stroke: .4rem $color_gray;
        }
      }
    }
  }
}